import { Component, OnInit } from '@angular/core';
import { RestService } from '../provider/rest.service';
import { NavController, AlertController } from '@ionic/angular';
import { Location } from "@angular/common";

@Component({
  selector: 'app-profile',
  templateUrl: './profile.page.html',
  styleUrls: ['./profile.page.scss'],
})
export class ProfilePage implements OnInit {
  segment: any = 0;
  profileData: any = [];
  response: any;
  preFix: string = localStorage.getItem('urlPrefix');
  currentPass: any;
  newPass: any;
  retypPass: any;
  constructor(public alertController: AlertController, private RestService: RestService, public navCtrl: NavController, private location: Location) { }

  ngOnInit() {
    this.getProfileData();
  }

  segmentChanged(ev: any) {
    // console.log('Segment changed', ev);
  }

  goBack() {
    this.location.back();
  }

  getProfileData() {
    var Token = JSON.parse(localStorage.userData).Token;
    this.RestService.requestViaGet('app/EngineerProfile/GetEngineerProfile/' + Token, '').then((response) => {
      //console.log(response);
      this.response = response;
      if (this.response.Message == "success") {
        this.profileData = this.response.result[0];
        localStorage.setItem('profileData', JSON.stringify(this.profileData));
      } else {

      }
    });
  }

  async changePass() {

    if (this.currentPass == '' || this.currentPass == undefined) {
      const alert = await this.alertController.create({
        cssClass:'cstmAlert',
        message: 'Please Enter Current Password.',
        buttons: ['OK']
      });
      await alert.present();
      return;
    }

    if (this.newPass == '' || this.newPass == undefined) {
      const alert = await this.alertController.create({
        cssClass:'cstmAlert',
        message: 'Please Enter New Password.',
        buttons: ['OK']
      });
      await alert.present();
      return;
    }

    if (this.retypPass == '' || this.retypPass == undefined) {
      const alert = await this.alertController.create({
        cssClass:'cstmAlert',
        message: 'Please Enter Confirm Password.',
        buttons: ['OK']
      });
      await alert.present();
      return;
    }
    if (this.newPass != this.retypPass) {
      const alert = await this.alertController.create({
        header: '',
        message: 'New Password and retype password are not same',
        cssClass: 'required',
        buttons: [
          {
            text: 'Ok',
            role: 'ok',
            handler: (blah) => {
              console.log('Confirm Cancel: blah');
            }
          }
        ]
      });

      await alert.present();
      return;

    }
    var data = {
      "UserName": "",
      "OTP": "",
      "OldPassword": this.currentPass,
      "NewPassword": this.retypPass
    };
    var Token = JSON.parse(localStorage.userData).Token;
    this.RestService.postServices('app/EngineerProfile/PasswordChange/' + Token, data).then(async (response) => {
      console.log(response);
      this.response = response;
      const alert = await this.alertController.create({
        header: '',
        message: this.response.MessageDescription,
        cssClass: 'secondary',
        buttons: [
          {
            text: 'Ok',
            role: 'ok',
            handler: (blah) => {
              console.log('Confirm Cancel: blah');
            }
          }
        ]
      });

      await alert.present();
      // alert(this.response.MessageDescription);
      if (this.response.Message == "success") {
        this.navCtrl.navigateForward("/login");
      } else {
        const alert = await this.alertController.create({
          header: '',
          message: this.response.MessageDescription,
          cssClass: 'required',
          buttons: [
            {
              text: 'Ok',
              role: 'ok',
              handler: (blah) => {
                console.log('Confirm Cancel: blah');
              }
            }
          ]
        });
  
        await alert.present();
      }
      //  alert(response.MessageDescription);
    });
  }
}
